/* Reset Overlay, Container, Title, Content(div) & Close button */
.pum-overlay,
.pum-container,
.pum-title,
.pum-content,
.pum-content + .pum-close,
.pum-content + .pum-close:hover,
.pum-content + .pum-close:focus,
.pum-content + .pum-close:active {
  background: none;
  border: none;
  bottom: auto;
  clear: none;
  cursor: default;
  /* didn't really know what the default for display should be*/
  /*display:inline;*/
  float: none;
  font-family: inherit;
  font-size: medium;
  font-style: normal;
  font-weight: normal;
  height: auto;
  left: auto;
  letter-spacing: normal;
  line-height: normal;
  max-height: none;
  max-width: none;
  min-height: 0;
  min-width: 0;
  overflow: visible;
  position: static;
  right: auto;
  text-align: left;
  text-decoration: none;
  text-indent: 0;
  text-transform: none;
  top: auto;
  visibility: visible;
  white-space: normal;
  width: auto;
  z-index: auto;
}

.pum-title,
.pum-content {
  position: relative;
  z-index: 1;
}

.pum-overlay {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1999999999;
  overflow: auto;
  overflow: initial;
  display: none;
  /**
  * Use border-box for all popup content. Providing more precise sizing.
  */
}

.pum-overlay.pum-preview, .pum-overlay.pum-form-submission-detected {
  display: block;
}

.pum-overlay, .pum-overlay:before, .pum-overlay:after,
.pum-overlay *, .pum-overlay *:before, .pum-overlay *:after {
  -webkit-box-sizing: border-box;
  /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;
  /* Firefox, other Gecko */
  box-sizing: border-box;
}

.pum-container {
  top: 100px;
  position: absolute;
  margin-bottom: 3em;
  z-index: 1999999999;
}

.pum-container.pum-responsive {
  left: 50%;
  margin-left: -47.5%;
  width: 95%;
  height: auto;
  overflow: visible;
}

.pum-container.pum-responsive img {
  max-width: 100%;
  height: auto;
}

@media only screen and (min-width: 1024px) {
  .pum-container.pum-responsive.pum-responsive-nano {
    margin-left: -5%;
    width: 10%;
  }
  .pum-container.pum-responsive.pum-responsive-micro {
    margin-left: -10%;
    width: 20%;
  }
  .pum-container.pum-responsive.pum-responsive-tiny {
    margin-left: -15%;
    width: 30%;
  }
  .pum-container.pum-responsive.pum-responsive-small {
    margin-left: -20%;
    width: 40%;
  }
  .pum-container.pum-responsive.pum-responsive-medium {
    margin-left: -30%;
    width: 60%;
  }
  .pum-container.pum-responsive.pum-responsive-normal {
    margin-left: -30%;
    width: 70%;
  }
  .pum-container.pum-responsive.pum-responsive-large {
    margin-left: -35%;
    width: 80%;
  }
  .pum-container.pum-responsive.pum-responsive-xlarge {
    margin-left: -47.5%;
    width: 95%;
  }
  .pum-container.pum-responsive.pum-position-fixed {
    position: fixed;
  }
}

@media only screen and (max-width: 1024px) {
  .pum-container.pum-responsive.pum-position-fixed {
    position: absolute;
  }
}

.pum-container.custom-position {
  left: auto;
  top: auto;
  margin-left: inherit;
}

.pum-container .pum-content > :last-child {
  margin-bottom: 0;
}

.pum-container .pum-content + .pum-close {
  text-decoration: none;
  text-align: center;
  line-height: 1;
  position: absolute;
  cursor: pointer;
  min-width: 1em;
  z-index: 2;
}

.pum-container .pum-content + .pum-close > span {
  position: relative;
  z-index: 1;
}

.pum-container.pum-scrollable .pum-content {
  overflow: auto;
  overflow-y: scroll;
  max-height: 95%;
}

.pum-overlay-disabled {
  visibility: hidden;
}

.pum-overlay-disabled::-webkit-scrollbar {
  display: block;
}

.pum-overlay-disabled .pum-container {
  visibility: visible;
}

html.pum-open.pum-open-overlay {
  overflow: hidden;
}

html.pum-open.pum-open-overlay.pum-open-fixed .pum-overlay {
  overflow: hidden;
}

html.pum-open.pum-open-overlay.pum-open-fixed .pum-container {
  position: fixed;
}

html.pum-open.pum-open-overlay.pum-open-scrollable body > *[aria-hidden] {
  padding-right: 15px;
}

html.pum-open.pum-open-overlay.pum-open-scrollable .pum-overlay.pum-active {
  overflow-y: scroll;
}

html.pum-open.pum-open-overlay-disabled.pum-open-fixed .pum-container {
  position: fixed;
}

html.pum-open.pum-open-overlay-disabled.pum-open-scrollable .pum-overlay.pum-active {
  position: static;
  height: auto;
  width: auto;
}

.pum-container iframe.formidable {
  width: 100%;
  overflow: visible;
}

body div#ui-datepicker-div[style] {
  z-index: 9999999999 !important;
}

.powered-by-popmake {
  bottom: -28px;
  display: block;
  left: 0;
  margin: 0 auto;
  position: absolute;
  text-align: center;
  width: 100%;
}

.powered-by-popmake > a {
  display: inline-block;
  max-width: 205px;
}

.powered-by-popmake img {
  max-width: 100%;
  width: 100%;
}

.powered-by-popmake.small {
  bottom: -22px;
}

.powered-by-popmake.small > a {
  max-width: 125px;
}

.powered-by-popmake.large {
  bottom: -30px;
}

.powered-by-popmake.large > a {
  max-width: 285px;
}

.popmake-close {
  cursor: pointer;
}

